<template>
  <div class="index">
    <div class="header">
      <div class="swiper-container">
        <div class="swiper-wrapper">
          <div class="swiper-slide">
            <router-link to="/index/" tag="a" :class=" (respath=='' || respath==undefined)?'itemStyle':'' ">
              全部
            </router-link>
          </div>
          <div class="swiper-slide">
            <router-link to="/index/good" tag="a" :class=" respath=='good'?'itemStyle':'' ">
              精华
              </router-link>
          </div>
          <div class="swiper-slide">
            <router-link to="/index/weex" tag="a" :class=" respath=='weex'?'itemStyle':'' ">
              WEEX
            </router-link>
          </div>
          <div class="swiper-slide">
            <router-link to="/index/share" tag="a" :class=" respath=='share'?'itemStyle':'' ">
              分享
            </router-link>
          </div>
          <div class="swiper-slide">
            <router-link to="/index/ask" tag="a" :class=" respath=='ask'?'itemStyle':'' ">
              问答
              </router-link>
          </div>
          <div class="swiper-slide">
            <router-link to="/index/job" tag="a" :class=" respath=='job'?'itemStyle':'' ">
              招聘
            </router-link>
          </div>
        </div>
      </div>
    </div>
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'Index',
  computed:{
    // 计算路径
    respath:function(){
      // 把路径分割成数组
      var pathArr = this.$route.path.split('/');
      return pathArr[2];
    }
  },
   mounted(){
    // 自己的 js 就写在这里面了
    var swiper = new Swiper('.swiper-container', {
      slidesPerView: 6,
      spaceBetween: 20,
    }); 
  }
}
</script>
<style lang="scss">
@import '../assets/sass/base.scss';
  .header{
    position: fixed;
    left:0;
    top: 0;
    width: 100%;
    height: rem(80px);
    background:#F6F6F6;
    z-index: 10;
    // swiper 的导航组件
    .swiper-container{
      .swiper-slide{
        font-size: rem(34px);
        text-align: center;
        a{
          line-height: rem(80px);
          color: #369219;
          padding: rem(3px) rem(10px);
          border-radius: rem(5px);
        }
        .itemStyle{
          background: #369219;
          color: #fff;          
        }
      }
    }
  }
</style>



