<template>
    <div class="content">
        <div v-html="dlist.content"></div>
    </div>
</template>
<script>
import {getcontent} from '@/getdata/index.js'
export default {
    name:'Content',
    data(){
        return {
            dlist:[]
        }

    }, 
    mounted(){
         console.log(this.$route.params.id);
        getcontent(this.$route.params.id).then((data)=>{
            this.dlist=data.data.data;
        })
    }
}
</script>
<style lang="scss">
@import '../../assets/sass/base.scss';
    .content{
        margin-top: rem(80px);
    }
</style>





