 <template>
  <div id="app">
    <router-view/>
   <nav class="footer">
      <ul>
        <router-link to="/" tag="li" :class="respath=='index'?'bg':''">首页</router-link>
        <li @click="show=!show">公众号</li>
        <router-link to="/me" tag="li" :class="respath=='me'?'bg':''">关于我们</router-link>
      </ul>
    </nav>
    <div v-show="show" class="wx" @click.self="show=!show">
        <div class="con">
          <div class="img"><img src="./assets/wx.png" alt=""></div>
          <div class="jies">
            <p>Vue中文社区 独家公众号，面向前端爱好者，
               每日更新最有料的文章，最前沿的资讯,内容包含但不限于Vue,React,Angular,
               前端工程化...等各种"大保健"知识点,右上角点关注,老司机带你弯道超车,
               不定期更有各种福利赠送
            </p>
            <p>👈🏼 亲,微信扫一扫奴家</p>
          </div>
        </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'App',
  data(){
    return {
      show:false
      }
  },
  computed:{
    respath:function(){
      var pathArr=this.$route.path.split('/');
      return pathArr[1];
    }
  }
}

</script>

<style lang="scss">
 @import './assets/sass/base.scss';
  .footer{
    position: fixed;
    bottom:0;
    width: 100%;
    background:#333;
    height: rem(80px);
    ul{
      display: flex;
      justify-content: space-around;
      li{
        color: #fff;
        line-height: rem(80px);
        font-size: rem(35px);
      }
      li.bg{
        color:red;
      }
    }
  }
  .wx{
    position: fixed;
    top:0;
    width: 100%;
    height: 100%;
    z-index: 100;
    background:rgba(20,20,20,0.3);
    .con{
      background:#fff;
      margin-top: rem(300px);
      .img{
        width:rem(300px);
        margin: 0 auto;
        img{
          width: 100%;
        }
      }
    .jies{
      width: 80%;
      margin: 0 auto;
      p{
        font-size:rem(30px);

      }
    }
    }
  }
</style>


