<template>
    <div class="model">
        <div class="items">
            <div class="item" v-for="item in dlist">
                <img :src="item.author.avatar_url" alt="">
                <div class="top" v-if="item.top">置顶</div>
                <div class="top" v-else-if="item.good">精华</div>
                <div class="tab" :class="respath==item.tab?'hidden':''" v-else>{{tab[item.tab]}}</div>
                <router-link :to="'/index/content/'+item.id" class="title">{{item.title}}</router-link>
                <div class="tm">{{time(item.last_reply_at)}}</div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name:'ModeList',
    data(){
        return{
            tab:{
                weex: "weex",
                share: "分享",
                ask: "问答",
                job: "招聘"
            },
        }
    },
    //从父作用域将数据传给子组件
    props:['dlist'],
    //计算
    computed:{
        // 计算路径
        respath:function(){
        // 把路径分割成数组
        var pathArr = this.$route.path.split('/');
        return pathArr[2];
        },  
    },
    //方法
    methods:{
       time:function(data){
           var nowtime=new Date();
           var lasttime=new Date(data);
           var time=(nowtime.valueOf()-lasttime.valueOf())/(1000*60);
            if(time<1){
                var tj='少于一分钟';
            };
           if(1<time){
               var tj=Math.floor(time)+'分钟前';
           };
           if(1<=time/60){
               var tj=Math.floor(time/60)+'小时前';
           };
           if(1<time/(60*24)){
                var tj=Math.floor(time/(60*24))+'天前';
           };
            if(1<=time/(60*24*30)){
                var tj=Math.floor(time/(60*24*30))+'个月前';
           };
           return tj;
       }
    }
}
//接收  父组件传递过来的值

</script>
<style lang="scss">
@import "../../assets/sass/base.scss";
  .items{
   margin-top:rem(80px);
    .item{
        height: rem(100px);
        display: flex;
        align-items: center;
        padding: 0 rem(10px);
        width: 100%;
       img{
           width:rem(60px);
           height: rem(60px);
           margin-right: rem(15px);
       }
       .top{
           width: rem(65px);
           height:rem(40px);
           background: #509030;
           color: #fff;
           text-align: center;
           line-height: rem(40px);
           border-radius: rem(8px);
           margin-right: rem(15px);
       }
       .tab{
           width: rem(65px);
           height:rem(40px);
           background: #ccc;
           color: #fff;
           text-align: center;
           line-height: rem(40px);
           border-radius: rem(8px);
           margin-right: rem(15px);
       }
       .hidden{
           display: none;
       }
       .title{
           color:#333;
           font-size: rem(28px);
           width:rem(480px);
           overflow: hidden;
           text-overflow: ellipsis;
            white-space: nowrap;
       }
       .tm{
           position: absolute;
           right: rem(15px);
       }
    }
    }
</style>





