import axios from 'axios';
//基本配置
var $http=axios.create({
    baseURL: 'https://www.vue-js.com/api/v1/',
    headers: { 'X-Custom-Header': 'foobar' }
});
function getlist(a){
    var b=Object.assign({
        limit:10
    },a);
    return $http({
        method:'get',
        url:'/topics',
        params: b,
    })
}
function getcontent(id){

    return $http({
        method:'get',
        url:'/topic/'+id,
    })
}
export{getlist,getcontent}



