<template>
    <div class="all">
        <mode-list :dlist='dlist'></mode-list>
        <infinite-loading @infinite="infiniteHandler"></infinite-loading>
    </div>
</template>
<script>
import {getlist} from '@/getdata/index.js'
import ModeList from '@/components/topics/ModeList'
import InfiniteLoading from 'vue-infinite-loading'
export default {
    name:'All',
    data(){
        return {
            dlist:[],
            page:1
        }

    },
    components:{
        ModeList,
        InfiniteLoading
        
    },
    mounted(){
        getlist().then((data)=>{
            this.dlist=data.data.data;
            console.log(data.data.data)
        })
    },
     methods: {
    infiniteHandler($state) {
      setTimeout(() => {

        getlist({limit:10,page:++(this.page)}).then(data=>{
          // console.log(data) 
          this.dlist = this.dlist.concat(data.data.data);
        })

        // 重新请求数据
        $state.loaded();  
      }, 1000);
    },
  },
}
</script>
<style lang="scss">

</style>





